
/* ==================== common styles ======================= */



*,
*:before,
*:after {
  box-sizing: border-box;
}

.hidden {
  display: none;
}

a {
  text-decoration: none;
}

img {
  outline: none;
  border: none;
}

section {
  position: relative;
  padding: 20px 0;
}

.sliderWrapper {
  position: relative;
  width: 1920px;
  max-width: 100%;
  height: 100%;
  margin: 0 auto;
}

.mainSlider {
  position: relative;
  height: 850px;
  background: #f9b00b;
  margin-bottom: 150px;
}

.item {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  align-content: center;
  position: relative;
  width: 100%;
  height: 850px;
  overflow: hidden;
}

.slideBg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-size: cover;
}

.slideContent {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  align-content: center;
  justify-content: space-between;
  flex-grow: 1;
  padding: 2%;
}

.layer {
  transform: translateX(300%);
  transition-property: transform;
}

.layerBack_0 {
  transition-duration: 3.5s;
}
.layerBack_1 {
  transition-duration: 5.5s;
}
.layerBack_2 {
  transition-duration: 7s;
}
.layerBack_3 {
  transition-duration: 5s;
}

.layerFront_0 {
  transition-duration: 4.5s;
}
.layerFront_1 {
  transition-duration: 8s;
}

.layerBack,
.layerFront {
  transition-timing-function: cubic-bezier(.33,1.67,.5,.78);
}

.slide_2 {
    .slideBg {
        background-image: url(../img/slide2/slide2bg.jpg);
      }
    .layerBack_0 {
      top: -2%;
      left: 85%;
    }
    .layerBack_1 {
      top: 92%;
      left: 3%;
    }
    .layerFront_0 {
      top: 11%;
      left: -24%;
    }
    .layerFront_1 {
      top: -24%;
      left: 8%;
    }
}

.slide_3 {
    .slideBg {
        background-image: url(../img/slide3/slide3bg.jpg);
      }
    .layerBack_0 {
      top: -8%;
      left: 11%;
    }
    .layerBack_1 {
      top: -2%;
      left: 81%;
    }
    .layerBack_2 {
      top: 66%;
      left: -18%;
    }
    .layerBack_3 {
      top: 39%;
      left: 87%;
    }
    .layerFront_0 {
      top: 11%;
      left: -24%;
    }
    .layerFront_1 {
      top: 68%;
      left: 56%;
    }
}

.slide_4 {
    .slideBg {
        background-image: url(../img/slide4/slide4bg.jpg);
      }
    .layerBack_0 {
      top: -10%;
      left: 29%;
    }
    .layerBack_1 {
      top: 33%;
      left: -21%;
    }
    .layerBack_2 {
      top: 13%;
      left: 89%;
    }
    .layerFront_0 {
      top: 71%;
      left: 74%;
    }
    .layerFront_1 {
      top: 70%;
      left: 5%;
    }
}

.slide_5 {
    .slideBg {
        background-image: url(../img/slide5/slide5bg.jpg);
      }
    .layerBack_1 {
      top: -5%;
      left: 48%;
    }
    .layerFront_0 {
      top: 21%;
      left: -24%;
    }
    .layerFront_1 {
      top: 27%;
      left: 53%;
    }
}







.owl-item.active {
  .layer {
    transform: none !important;
  }
  
}

.slideImages {
  position: relative;
  outline: 3px solid white;
  //min-width: 50%;
  max-height: 100%;
  margin-right: 10%;

  img {
    max-height: 100%;
  }
}





.layer {
  position: absolute;
  z-index: 2;
  width: auto !important;
}

.layerBack {
  z-index: 2;
  top: 1%;
  left: 1%;
}

.layerFront {
  z-index: 4;
  top: 1%;
  left: 1%;
}

.layerMain {
  position: relative;
  z-index: 3;
  transition: all 1.5s cubic-bezier(.42,1.41,.42,1.02);

}



.info {
  z-index: 100;
}

.mainSlider .item h3 {
  color: white;
  font-size: 90px;
  font-weight: 800;
  line-height: 90px;
  margin-bottom: 20px;
}

.mainSlider .item p {
  color: white;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  width: 240px;
  margin-bottom: 30px;
}

.btn {
  width: 240px;
  height: 70px;
  border-radius: 35px;
  background-color: #4e1e02;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#6b301e), to(#4e1e02));
  background-image: linear-gradient(to bottom, #6b301e 0%, #4e1e02 100%);
  text-align: center;
  color: #ffffff;
  font-family: Geometria;
  font-size: 18px;
  font-weight: 700;
  -webkit-box-shadow: -15px 26px 30px rgba(78, 30, 2, 0.25);
  box-shadow: -15px 26px 30px rgba(78, 30, 2, 0.25);
  border: none;
  display: inline-block;
  cursor: pointer;
}

.mainSlider .item .btn {
  line-height: 70px;
}